@use "../variables" as *;
@use "../utils/mixins" as *;

//
// modal.scss
//

.modal-content {
    box-shadow: $box-shadow;
    background-color: $white;
}
.modal-title {
    color: inherit;
    font-size: 18px;
}
.modal-full-width {
    width: 95%;
    max-width: none;
}
.modal-top {
    margin: 0 auto;
}
.modal {
    &.show {
        .modal-right,
        .modal-left {
            transform: translate(0, 0) !important;
        }
    }
}
.modal-bottom {
    display: flex;
    flex-flow: column nowrap;
    -ms-flex-pack: end;
    justify-content: flex-end;
    height: 100%;
    margin: 0 auto;
    align-content: center;
}
.modal-filled {
    color: $white;
    .modal-header {
        background-color: rgba($white, 0.1);
    }
    .modal-title {
        color: $white;
    }
    .modal-header,
    .modal-footer {
        border: none;
    }
}
.modal-backdrop {
    backdrop-filter: blur(10px);
}
.modal-header {
    padding: 16px 20px;
}
.modal-body {
    padding: 20px;
    background-color: transparent;
}
.modal-footer>* {
    margin-top: 0;
    margin-bottom: 0;
}
.modal-footer {
    padding: 16px 20px;
}
