  }
  
  .flatpickr-weekwrapper .flatpickr-weeks {
    box-shadow: 1px 0 0 $border-color ;
  }
  
  /* End:Pickers */

  /* Start:: swiper js */
.swiper {
    border-radius: 5px;
  }
  
  [dir="rtl"] {
    .swiper {
      direction: ltr;
    }
  
    .swiper-backface-hidden .swiper-slide {
      direction: rtl;
    }
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .swiper-button-next,
  .swiper-button-prev {
    width: 1.563rem ;
    height: 1.563rem ;
    color: $white ;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 5px;
  }
  
  .swiper-button-next:after,
  .swiper-button-prev:after {
    font-size: 0.75rem ;
    font-weight: 800 ;
  }
  
  .swiper-horizontal1 .swiper-slide {
    height: auto;
  }
  
  .swiper-pagination-bullet {
    width: 1.25rem ;
    height: 0.25rem ;
    border-radius: 5px ;
    background: $white ;
  }
  
  .swiper-pagination-bullet-active {
    background: $white ;
  }
  
  .swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
    background: $success ;
  }
  
  .swiper-pagination {
    color: $white;
  }
  
  .custom-pagination {
    .swiper-pagination-bullet {
      width: 1.5rem ;
      height: 1.5rem ;
      padding: 0.188rem;
      background-color: rgba(255, 255, 255, 0.3) ;
      color: $white ;
      opacity: 1 ;
  
      &.swiper-pagination-bullet-active {
        background-color: $white ;
        color: $black ;
      }
    }
  }
  
  .swiper-scrollbar {
    background-color: rgba(255, 255, 255, 0.3) ;
  }
  
  .swiper-scrollbar-drag {
    background-color: $white ;
  }
  
  .swiper.vertical {
    height: 21.875rem;
  }
  
  .swiper-preview {
    height: 80%;
    width: 100%;
  }
  
  .swiper-view {
    height: 20%;
    box-sizing: border-box;
    padding: 10px 0;
  }
  
  .swiper-view .swiper-slide {
    width: 25%;
    height: 100%;
    opacity: 0.4;
  }
  
  .swiper-view .swiper-slide-thumb-active {
    opacity: 1;
  }
  
  .swiper-preview .swiper-wrapper {
    margin-block-end: 0.65rem;
  }
  
  .swiper-thumbs .swiper-slide img {
    border-radius: 5px;
  }
  
  /* End:: swiper js */

